@import url(https://fonts.googleapis.com/css2?family=Style+Script&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;300;400&display=swap);
@media screen and (min-width: 993px) {
    .home-wrapper {
        margin: 2%;
        display: flex;
        justify-content: space-around;
        font-family: 'Montserrat', sans-serif;
    }
    
    .popular-destinations-wrapper {
        display: flex;
        justify-content: center;
    }
    
    .popular-destinations-wrapper > ul{
        list-style: none;
    }
    
    .home-headers {
        font-size: 1.75rem;
        text-align: center;
    }
    
    .popular-destination-items {
        margin-top: 3%;
        text-align: center;
        border: 1px solid rgba(112, 111, 111, 0.616);
        background-color: rgb(245, 245, 245);
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.15), 0 6px 20px 0 rgba(0, 0, 0, 0.01);
        border-radius: 20px;
        color: rgb(6, 3, 204);
        font-size: 1.15rem;
        width: 20vw;
        padding-left: 8%;
        padding-right: 8%;
    }
    .popular-destination-anchor {
        text-decoration: none;
    }
}

@media screen and (max-width: 992px) {
    .home-wrapper {
        margin: 2%;
        display: flex;
        justify-content: center;
        font-family: 'Montserrat', sans-serif;
        padding-bottom: 5%;
    }

    .popular-destinations-wrapper {
        display: flex;
        justify-content: center;
        flex-direction: column;
    }
    
    .popular-destinations-wrapper > ul{
        margin: 0;
        padding: 0;
        list-style: none;
    }
    
    .home-headers {
        font-size: 1.75rem;
        text-align: center;
    }
    
    .popular-destination-items {
        margin-top: 3%;
        text-align: center;
        border: 1px solid rgba(112, 111, 111, 0.616);
        background-color: rgb(245, 245, 245);
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.15), 0 6px 20px 0 rgba(0, 0, 0, 0.01);
        border-radius: 20px;
        color: rgb(6, 3, 204);
        font-size: 1rem;
        /* width: 20vw; */
        /* margin-left: 8%;
        margin-right: 8%; */
    }
    .popular-destination-anchor {
        text-decoration: none;
    }
}


@media screen and (min-width: 993px) {
    
#root {
    /* background: rgba(142, 169, 241, 0.75); */
    background-image: url(/static/media/beach.6b1c10db.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.content-wrapper {
    margin: 3%;
    width: 80%;
    height: 75%;
    background-color: #ffffff;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    border-radius: 7px;
}

.header {
    font-family: 'Style Script', cursive;
    font-size: 4rem;
    text-align: center;
    padding: 1%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 10%;
}

.paperairplane {
    width: 11vw;
    height: 11vh;
    background-image: url(/static/media/paperairplane.1c6d45a6.png);
    background-repeat: no-repeat;
    background-size: contain;
}

.header-link {
    text-decoration: none;
    color: rgba(0, 0, 0, 0.9);
}

.search-wrapper {
    display: flex;
    justify-content: center;
    margin: 1%;
    padding: 1%;
}

.search-wrapper > div {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.15), 0 6px 20px 0 rgba(0, 0, 0, 0.01);
}
}

